@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');

body
{
    text-align: center;
    /* background: linear-gradient(	#080808, #484848) no-repeat; */
    background:	rgb(0,0,0) !important;
    color: 	#FFFF !important;
    font-family: 'Cinzel', serif !important;
}
.test
{
    color:#FFFF;
}
.nav-link
{
    color:#C0C0C0;
    font-size: 200%;
}
.nav-link:hover
{
    color:#C0C0C0;
    text-decoration: underline !important;
}
.nav
{
    margin-bottom: 3% !important;
}
h1
{
    margin-top: 2% !important;
    font-size: 400% !important;
}
h2
{
    margin-top: 10% !important;
    font-size: 300% !important;
}
#haut
{
    background:url(/img/pexels-elijah-odonnell-3473569.jpg)no-repeat;
    background-size: 100%;
    height: 100vh;
}
#accueil
{
    display:flex !important;
    margin-top: 15% !important;
    margin-left: 10%;
}
.barre
{
    align-self: center;
    height: 3vh;
    width:1vw;
    background-color: #FFFF;
    margin-right: 2%;
    animation: grow;
    animation-fill-mode: forwards;
    animation-duration: 5s;
}

@keyframes grow {
    0% {
     height:3vh;
    }
    100% {
      height:20vh;
    }
}

#about
{
    background:url(/img/pexels-yurii-hlei-1365795.jpg)no-repeat;
    background-size: 100%;
    height: 100vh;
    display:flex;
    flex-direction: column;
    align-items: flex-end;
}

#about h3
{
    margin: 10% 5% 0% 0%;
    font-size: 300% !important;
    font-weight: bold;
    letter-spacing: 2% !important;
}
.underline
{
    height:0.5vh;
    width:4.7vw;
    background-color: #FFFF;
    margin-bottom: 2%;
    margin-right: 12%;
}

.text
{
    display:flex;
    justify-content: flex-end;
}

#about p
{
    width:50%;
    margin-right: 5%;
    font-size: 120% !important;
    font-weight: 500;
    background-color:rgba(0, 0, 0, 0.32);
    padding: 0.1%;
}
.barre2
{
    margin-top: 2%;
    margin-right: 0.7%;
    height: 10vh;
    width:0.3vw;
    background-color: #FFFF;
}


#cv
{
    background:url(/img/pexels-adrien-olichon-2387793.jpg)no-repeat;
    background-size: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#cv h3
{
    font-size: 300% !important;
    font-weight: bold;
    margin: 3% 0%;
}

#cv p
{
    border: solid 0.08em #FFFF;
    width: 25%;
    text-align: center;
    font-size: 150% !important;
    font-weight: bold;
    padding: 1% 0%;
    margin: 0 !important;
}
.cv
{
    border:none !important;
    padding-top: 4% !important;
}

#cv a
{
    color:#b1b1b1;
    font-weight: lighter;
}

#cv a:hover
{
    text-decoration: underline;
}

.barre3
{
    height: 6vh;
    width:0.1vw;
    background-color: #FFFF;
}


#projets
{
    background:url(/img/pexels-junior-teixeira-2047905.jpg)no-repeat;
    background-size: 100%;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.video
{
    margin: 2%;
    width:40%;
    text-align: center;
}

#contact
{
    background:url(/img/pexels-daria-shevtsova-1356300.jpg)no-repeat;
    background-size: 100%;
    height: 100vh;
}
#contact p
{
    text-align: center;
    padding-top: 5%;
}
.snLink
{
    display: flex;
    justify-content: space-around;
    margin-top: 2%;
}
#contact i
{
    color: #FFFF;
}
footer
{
    position: sticky;
    bottom: 2%;
    width: 10%;
}
footer a
{
    color: #FFFF;
    font-size: 400%;
    font-weight: bold;
    margin-left:80%;
}
footer a:hover
{
    text-decoration: none;
    color: #b1b1b1;
}